                p.color-adicional-4 La comunicación con la comunidad debe realizarse preferiblemente en su lengua nativa (si aplica) y en formatos accesibles —orales, visuales o performativos—, no solo mediante informes escritos.
                p.color-adicional-4 Es fundamental reconocer los niveles de alfabetización, las tradiciones orales y los canales comunitarios tradicionales. En algunos casos, puede ser más efectivo utilizar teatro comunitario,  radionovelas o encuentros alrededor del fogón que documentos formales. Estos espacios participativos permiten transmitir las novedades, fortalecer los lazos sociales y preservar las dinámicas culturales del territorio.
            
